  position: relative;
  height: 55%;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  transform: rotate(0) translate(0, 0);
}
.budget-creation-container--sub-categories__main-category-display__left-button, .budget-creation-container--sub-categories__main-category-display__right-button {
  position: relative;
  height: 100%;
  width: 20%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  transform: rotate(0) translate(0, 0);
}
.budget-creation-container--sub-categories__main-category-display__left-button__icon:hover, .budget-creation-container--sub-categories__main-category-display__right-button__icon:hover {
  cursor: pointer;
  transition: color 0.5s;
}
.budget-creation-container--sub-categories__main-category-display__category-information {
  position: relative;
  height: 100%;
  width: 60%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  transform: rotate(0) translate(0, 0);
}
.budget-creation-container--sub-categories__sub-category-display {
  position: relative;
  height: 45%;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  transform: rotate(0) translate(0, 0);
  border-top: 0.3rem solid #822aba;
  overflow-y: auto;
