.category-creation-controller {
  position: relative;
  height: 100%;
  width: 30%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  transform: rotate(0) translate(0, 0);
