.icons-container {
  position: relative;
  height: 80%;
  width: 100%;
  display: grid;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  transform: rotate(0) translate(0, 0);
  border: 0.3rem solid #822aba;
  grid-template-columns: repeat(4, 1fr);
  border-top-left-radius: 0.9rem;
  border-top-right-radius: 0.9rem;
  overflow-y: auto;
}
.icons-container .icon-container {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  transform: rotate(0) translate(0, 0);
  padding: 3rem 0;
  z-index: 3;
}
.icons-container .icon-container:first-of-type {
  border-top-left-radius: 0.5rem;
}
.icons-container .icon-container:hover {
  cursor: pointer;
  background-color: #822aba;
  transition: background-color 0.5s, color 0.5s;
}
.icons-container .icon-container__icon {
  position: relative;
  z-index: 0;
  margin-bottom: 0.5rem;
}
.icons-container .icon-container--clicked {
  background-color: #822aba;
}

.main-category, .main-category__alt {
  position: relative;
  height: 25%;
  width: 25%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  transform: rotate(0) translate(0, 0);
  border: 0.3rem solid rgba(130, 42, 186, 0.8);
}
.main-category:first-of-type, .main-category__alt:first-of-type {
  border-top-left-radius: 0.9rem;
}
.main-category:nth-of-type(3), .main-category__alt:nth-of-type(3) {
